import './App.css';
import Header from './components/Header'
import  HomeCarousel from './components/HomeCarousel'
import ItemCards from './components/ItemCards'
import Footer from './components/Footer'
import Features from './components/Features'
import ContactForm from './components/ContactForm'
import IndivProduct from './components/IndivProduct'
import AllProds from './components/AllProducts'
import Login from './components/Login'
import Checkout from './components/Checkout'
import 'bootstrap/dist/css/bootstrap.min.css';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import axios from 'axios';
import {URLS} from './components/urls'
import Cookies from 'universal-cookie';
import React, { useState, useEffect } from 'react'
import Registration from './components/Registration';
import CheckoutResults from './components/CheckoutResults';
import AccountDashboard from './components/Dashboard/AccountDashboard'

function App() {
  const [categories, setcategories] = useState([])
  const [loggedIn, setloggedIn] = useState(false)
  
  // States for routing 
  const [prodID, setprodID] = useState()
  const [catID, setcatID] = useState()
  const [subCatID, setsubCatID] = useState()

  //Cart 
  const [cart, setcart] = useState({})

  //State for searching Item
  const [searchedItem, setsearchedItem] = useState()



  const cookies = new Cookies();


    useEffect(() => {
      if (cookies.get('username') != undefined){
        setloggedIn(true)
      }
    }, [loggedIn])

    


  useEffect(()=>{
    async function fetchData() {
        const request = await axios.get(`http://${URLS}/fetchAllCategories`)
        setcategories(request.data)
    
    }
    
    fetchData()
    
}, [])

useEffect(() => {
  window.scrollTo(0, 0);
}, []);


  return (
    <div className="App">
      <Router>
        <Header category={setcatID} setprodID={setprodID} subcategory={setsubCatID} searchedItem={searchedItem} setsearchedItem={setsearchedItem} cart={cart} setcart={setcart}/>
        <Switch>
          <Route exact path='/'>
            <HomeCarousel/>
            {categories.map(
              category => (
                <>
                <ItemCards category={category} setprodID={setprodID} setcatID={setcatID} setsubCatID={setsubCatID} cart={cart} setcart={setcart}/>
                <Features/>
                </>
                  )
            )}
            <ContactForm/>
          </Route>
          <Route exact path='/allProducts'>
            <AllProds/>
          </Route>
          <Route exact path='/products/:category'>
              <AllProds catID={catID} setcatID={setcatID} setsubCatID={setsubCatID} cart={cart} setcart={setcart}/>
          </Route>
          <Route exact path='/products/:category/:subcategory'>
            <AllProds catID={catID} subCatID={subCatID} setcatID={setcatID} setsubCatID={setsubCatID} setprodID={setprodID} cart={cart} setcart={setcart}/>
          </Route>
          <Route exact path='/products/:category/:subcategory/:product'>
            <IndivProduct catID={catID} subCatID={subCatID} prodID={prodID} cart={cart} setcart={setcart}/>
          </Route>

          <Route exact path='/checkout/:user'>
            {
              loggedIn
              ?
              <Checkout/>
              :
              <Login/>
            }
          </Route>
          <Route exact path='/login'>
              <Login/>            
          </Route>
          <Route exact path='/registration'>
            {
              loggedIn === false
              ?
              <Registration/>
              :
              <h3 className='mt-3 text-center'>Please log out to create a new user</h3>
            }
          </Route>
          <Route exact path='/checkoutSuccess'>
            <CheckoutResults status='success' urlStatus='Success' message={`Successfully purchased your product. Please visit your account log for more details.`}/>
          </Route> 
          <Route exact path='/checkoutFail'>
            <CheckoutResults status='danger' urlStatus='Fail' message={`Failed to purchase your product. Please visit your account log for more details. For more information, please send us a message.`}/>
          </Route>  
          <Route exact path='/myAccount/:type'>
            {
              loggedIn === false?
              <Login/> : 
              <AccountDashboard/>
            }
              
          </Route>
          
        </Switch>
      </Router>
      <Footer/> 
    </div>
  );
}

export default App;
